<!-- eslint-disable max-len -->
<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item"><a class="pagination__link pagination__link--arrow"
        :class="{ 'pagination__link--disabled': pageNumber === 1 }" aria-label="Предыдущая страница"
        @click.prevent="paginatePrev(pageNumber)" @keydown.enter.prevent="paginatePrev(pageNumber)"
        @keydown.space.prevent="paginatePrev(pageNumber)"><svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg></a></li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber"><a href="#" class="pagination__link"
        :class="{ 'pagination__link--current': pageNumber === page }" @click.prevent="paginate(pageNumber)">{{ pageNumber
        }}</a>
    </li>
    <li class="pagination__item"><a class="pagination__link pagination__link--arrow" href="#"
        aria-label="Следующая страница" @click.prevent="paginateNext(pageNumber)"><svg width="8" height="14"
          fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg></a></li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
    pageNumber() {
      return this.page;
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
    paginatePrev() {
      if (this.page > 1) {
        this.paginate(this.page - 1);
      }
    },
    paginateNext() {
      if (this.page < this.pages) {
        this.paginate(this.page + 1);
      }
    },
  },
};
</script>
